<template>
  <div class="flex items-center justify-between w-full mb-4">
    <div class="w-1/4 text-xs">
      <h1>Cloro libre <span class="font-bold text-xxs">(ppm)</span></h1>
    </div>
    <input
      class="inputtext"
      type="number"
      placeholder="----"
      v-model="parametro.actual"
      @change="parametro.diferencia = parametro.actual - parametro.deseado"
    />
    <input
      class="inputtext"
      type="number"
      placeholder="----"
      v-model="parametro.deseado"
      @change="parametro.diferencia = parametro.actual - parametro.deseado"
    />
    <div class="w-1/4 text-sm">
      <h1>{{ parametro.diferencia }}</h1>
    </div>
  </div>
</template>
<script>
import { ref } from "@vue/reactivity";
export default {
  props: {
    nombre: {
      type: String,
      required: false,
    },
  },
  setup(props) {
    const parametro = ref({ actual: 0, deseado: 0, diferencia: 0 });
    return { parametro };
  },
};
</script>
<style scoped>
.text-bluedemon-500 {
  color: #1f5eff;
}

.inputtext {
  @apply w-1/4 text-sm text-center border-b-2 p-1 mx-2;
}
.inputtext:focus {
  outline: none;
  @apply border-blue-500 font-bold text-bluedemon-500;
}
</style>
